}
.scrollTop {
    width: 100%;
    margin: 0;
    padding: 1.5em 0;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
    -webkit-animation: fadeIn 0.3s;
            animation: fadeIn 0.3s;
    transition: opacity 0.4s;
    background-color: var(--clr-accent2);
    color: var(--clr-light);
    opacity: 0.8;
  }
  
  .scrollTop:hover {
    opacity: 0.7;
  }
  
  @-webkit-keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
  @keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 0.5;
    }
  }
  
